.MuiTub-root{
	min-width: 20px !important;
}
.actions-wrapper{
	border-bottom: 1px solid #8888882b;
	height: 48px;
}
.todo-header{
	height: 100px;
	background-color: #9ba5e8;
	position: relative;
	border-radius: 5px 5px 0 0;
}
.add-todo svg{
	position: absolute;
	width: 50px;
	height: 50px;
	z-index: 999;
	right: 25px;
	bottom: -25px;
}
.icon-background{
	position: absolute;
	background-color: #fff;
	width: 40px;
	height: 40px;
	right: 30px;
	bottom: -20px;
	border-radius: 50%;
}
.todos-count{
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 80px;
	padding: 20px 30px;
}
form{
	display: flex;
	flex-direction: column;
	height: 288px;
	justify-content: space-between;
	margin: 20px 0; 
}
.MuiDialog-paperWidthSm{
	width: 450px;
	min-height: 350px;
}

.MuiDialogTitle-root{
	border-bottom: 1px solid #8888882b;
	padding: 10px 0;
}
.todos-render-wrapper{
	overflow-x: hidden;
	overflow:auto;
	max-height: 258px;
}
.todo-container{
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 10px;
	height: 48px;
	border-bottom: 1px solid #8888882b;
}
.todo-item{
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	cursor: pointer;
}
.todo-finished{
	text-decoration: line-through;
	color: #8080808f;
}
.todo-wrapper {
  width: 500px;
  background-color: #fff;
  min-height: 300px;
  box-shadow: 0 0 5px rgb(0 0 0 / 50%);
  border-radius: 5px;
  margin: 20px auto;
}

/* ОБНУЛЕНИЕ СТИЛЕЙ */
*,
*::before,
*::after {
margin: 0;
padding: 0;
border: 0;
box-sizing: border-box;
}

li {
list-style: none;
}

a,
a:visited {
text-decoration: none;
}

a:hover {
text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
font-weight: inherit;
font-size: inherit;
}

img {
max-width: 100%;
display: block;
}

html,
body {
height: 100%;
}

html {
font-size: 16px;
}

body {
overflow-x: hidden;
}
