.todos-render-wrapper{
	overflow-x: hidden;
	overflow:auto;
	max-height: 258px;
}
.todo-container{
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 10px;
	height: 48px;
	border-bottom: 1px solid #8888882b;
}
.todo-item{
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	cursor: pointer;
}
.todo-finished{
	text-decoration: line-through;
	color: #8080808f;
}